import React, { useEffect, useState } from 'react';
import { useSearchParams, useNavigate } from 'react-router-dom';
import { Loader2, CheckCircle, XCircle, Mail } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { toast } from 'sonner';

const GmailCallback: React.FC = () => {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const [status, setStatus] = useState<'loading' | 'success' | 'error'>('loading');
  const [message, setMessage] = useState('Processing Gmail authentication...');

  useEffect(() => {
    const handleCallback = async () => {
      try {
        // Check if we're in an iframe (modal context)
        const isInIframe = window.self !== window.top;

        // Check for development success redirect
        const success = searchParams.get('success');
        const email = searchParams.get('email');
        const errorParam = searchParams.get('error');

        if (success === 'true' && email) {
          setStatus('success');
          setMessage('Gmail account connected successfully!');
          toast.success('Gmail account connected successfully!');

          // Check if we need to return to a specific workflow
          const savedState = localStorage.getItem('gmail_oauth_return_state');

          // Notify parent window if opened as popup or in iframe
          if (window.opener) {
            window.opener.postMessage({
              type: 'GMAIL_AUTH_SUCCESS',
              data: { email: decodeURIComponent(email) }
            }, window.location.origin);
            window.close();
          } else if (isInIframe && window.parent) {
            // Send message to parent window (modal)
            window.parent.postMessage({
              type: 'GMAIL_AUTH_SUCCESS',
              data: { email: decodeURIComponent(email) }
            }, window.location.origin);
          } else {
            // Handle redirect based on saved state
            if (savedState) {
              try {
                const workflowState = JSON.parse(savedState);
                console.log('ðŸ”„ Restoring complete workflow state:', workflowState);
                localStorage.removeItem('gmail_oauth_return_state');

                // Redirect back to the EXACT original URL with all parameters
                setTimeout(() => {
                  if (workflowState.fullUrl) {
                    console.log('ðŸŽ¯ Redirecting to exact URL:', workflowState.fullUrl);
                    window.location.href = workflowState.fullUrl;
                  } else if (workflowState.pathname && workflowState.search && workflowState.hash) {
                    // Fallback: reconstruct URL from parts
                    const reconstructedUrl = `${window.location.origin}${workflowState.pathname}${workflowState.search}${workflowState.hash}`;
                    console.log('ðŸ”§ Reconstructing URL:', reconstructedUrl);
                    window.location.href = reconstructedUrl;
                  } else {
                    // Last fallback
                    console.log('âš ï¸ Using fallback path:', workflowState.path || '/webhooks');
                    window.location.href = workflowState.path || `${window.location.origin}/#/webhooks`;
                  }
                }, 1500);
              } catch (e) {
                console.error('Error parsing saved state:', e);
                setTimeout(() => {
                  navigate('/webhooks');
                }, 2000);
              }
            } else {
              // Default redirect to webhooks page
              setTimeout(() => {
                navigate('/webhooks');
              }, 2000);
            }
          }
          return;
        }

        if (errorParam) {
          setStatus('error');
          setMessage(decodeURIComponent(errorParam));
          toast.error(`Gmail connection failed: ${decodeURIComponent(errorParam)}`);

          // Notify parent of error if in iframe
          if (isInIframe && window.parent) {
            window.parent.postMessage({
              type: 'GMAIL_AUTH_ERROR',
              message: decodeURIComponent(errorParam)
            }, window.location.origin);
          }
          return;
        }

        const code = searchParams.get('code');
        const state = searchParams.get('state');
        const error = searchParams.get('error');

        if (error) {
          setStatus('error');
          setMessage(`Authentication failed: ${error}`);
          toast.error('Gmail authentication was cancelled or failed');
          return;
        }

        if (!code || !state) {
          setStatus('error');
          setMessage('Missing authorization code or state parameter');
          toast.error('Invalid Gmail OAuth response');
          return;
        }

        // Exchange code for tokens
        const response = await fetch('/api/gmail/callback', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ code, state }),
        });

        const data = await response.json();

        if (response.ok && data.success) {
          setStatus('success');
          setMessage('Gmail account connected successfully!');
          toast.success('Gmail account connected successfully!');

          // Close the popup window and notify parent
          if (window.opener) {
            window.opener.postMessage({
              type: 'GMAIL_AUTH_SUCCESS',
              data: data.data
            }, window.location.origin);
            window.close();
          } else if (isInIframe && window.parent) {
            // Send message to parent window (modal)
            window.parent.postMessage({
              type: 'GMAIL_AUTH_SUCCESS',
              data: data.data
            }, window.location.origin);
          } else {
            // If not in popup or iframe, redirect to webhooks page
            setTimeout(() => {
              navigate('/webhooks');
            }, 2000);
          }
        } else {
          setStatus('error');
          setMessage(data.message || 'Failed to connect Gmail account');
          toast.error(data.message || 'Failed to connect Gmail account');

          // Notify parent of error if in iframe
          if (isInIframe && window.parent) {
            window.parent.postMessage({
              type: 'GMAIL_AUTH_ERROR',
              message: data.message || 'Failed to connect Gmail account'
            }, window.location.origin);
          }
        }
      } catch (error) {
        console.error('Gmail callback error:', error);
        setStatus('error');
        setMessage('An unexpected error occurred');
        toast.error('An unexpected error occurred');
      }
    };

    handleCallback();
  }, [searchParams, navigate]);

  const getStatusIcon = () => {
    switch (status) {
      case 'loading':
        return <Loader2 className="h-12 w-12 text-blue-600 animate-spin" />;
      case 'success':
        return <CheckCircle className="h-12 w-12 text-green-600" />;
      case 'error':
        return <XCircle className="h-12 w-12 text-red-600" />;
      default:
        return <Loader2 className="h-12 w-12 text-blue-600 animate-spin" />;
    }
  };

  const getStatusColor = () => {
    switch (status) {
      case 'loading':
        return 'text-blue-600';
      case 'success':
        return 'text-green-600';
      case 'error':
        return 'text-red-600';
      default:
        return 'text-blue-600';
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center p-4">
      <div className="max-w-md w-full bg-white rounded-lg shadow-lg p-8 text-center">
        <div className="mb-6">
          <div className="w-16 h-16 bg-red-100 rounded-full flex items-center justify-center mx-auto mb-4">
            <Mail className="h-8 w-8 text-red-600" />
          </div>
          <h1 className="text-2xl font-bold text-gray-900 mb-2">Gmail Authentication</h1>
          <p className="text-gray-600">Connecting your Gmail account...</p>
        </div>

        <div className="mb-6">
          <div className="flex justify-center mb-4">
            {getStatusIcon()}
          </div>
          <p className={`text-lg font-medium ${getStatusColor()}`}>
            {message}
          </p>
        </div>

        {status === 'error' && (
          <div className="space-y-4">
            <Button
              onClick={() => navigate('/webhooks')}
              className="w-full bg-red-600 hover:bg-red-700 text-white"
            >
              Return to Webhooks
            </Button>
            <Button
              variant="outline"
              onClick={() => window.location.reload()}
              className="w-full"
            >
              Try Again
            </Button>
          </div>
        )}

        {status === 'success' && (
          <div className="space-y-4">
            <div className="p-4 bg-green-50 border border-green-200 rounded-lg">
              <p className="text-sm text-green-800">
                Your Gmail account has been successfully connected. You can now use Gmail triggers and actions in your workflows.
              </p>
            </div>
            <Button
              onClick={() => navigate('/webhooks')}
              className="w-full bg-green-600 hover:bg-green-700 text-white"
            >
              Continue to Webhooks
            </Button>
          </div>
        )}

        {status === 'loading' && (
          <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
            <p className="text-sm text-blue-800">
              Please wait while we process your Gmail authentication...
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default GmailCallback;